* {
  box-sizing: border-box; }

html {
  font-size: 62.5%;
  /* 16px * 62.5 = 10px = 1rem */ }

body {
  margin: 0;
  height: 100vh;
  font: 1.6rem Helvetica, Arial; }

a {
  color: #000;
  text-decoration: none; }
  a:hover {
    color: #f08040; }

.grid-container {
  display: grid;
  grid-template-areas: 'header' 'main' 'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%; }

.header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background-color: #203040;
  color: #fff; }
  .header a {
    color: #fff;
    font-weight: bold;
    text-decoration: none; }
    .header a:hover {
      color: #f08040; }
  .header .brand a {
    font-size: 3rem; }

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #203040;
  color: #fff; }

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin: 0;
  list-style-type: none; }
  .products li {
    flex: 0 1 30rem;
    margin: 1rem;
    margin-bottom: 0;
    height: 42rem;
    border-top: 0.2rem #e0e0e0 solid; }
    .products li .product {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 100%; }
      .products li .product img {
        max-height: 26rem;
        max-width: 22rem;
        margin-top: 2rem; }
      .products li .product-name {
        line-height: 2.2rem; }
      .products li .product-brand {
        font-size: 1.2rem;
        color: #808080; }
      .products li .product-price {
        font-size: 2rem; }
